import FETCH_PLAYERS from '../actions/application'

const initial_state = [];

export default function reducer(state= initial_state, action) {
	switch(action.type) {
		case FETCH_PLAYERS:
			return [...state, action.payload];
		// case DELETE_PLAYER:
		// 	return {...state, players: }
		default:
			return state;
	}
}


